.coin-container {
    display: flex;
    justify-content: center;
}

.coin-row {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 300px;
}

.coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 200px;
}

.coin h1 {
    font-size: 16px;
    width: 200px;
}

.coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.coin-symbol {
    width: 100px;
    text-transform: uppercase
}

.coin-data {
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}

.coin-price {
    width: 30px;
}
